import React, { useState } from 'react'
import TiltPhaseSix from '../Components/TiltPhaseSix'
import { makeStyles } from '@material-ui/core/styles';
import * as Constant from '../Constants'
import { GridList} from '@material-ui/core';
import AnimatedCircle from '../Components/AnimatedCircle';

const customCardStyles = makeStyles((theme) => ({
    img: {
        margin: 0,
        padding: 0,
        display:"block",
        // filter: props => `opacity(.8) drop-shadow(0 0 0 ${props.img_color})`,
    },
    cardContainer:{
        position: "relative",
        textAlign: "center",
        margin:"5px",
    },
    text: {
        background: props => props.img_color,
        color: props => props.text_color,
        fontWeight:"bolder",
        // opacity:0.9,
        position: "absolute",
        bottom:"0%",
        marginBottom:0,
        padding:"10px 0 10px 0",
    },
}))

function CustomCard(props) {
    const classes = customCardStyles(props);
    const [cardWidth, setCardWidth] = React.useState(window.matchMedia("(orientation: landscape)").matches? "55vh" : "85vw");

    window.addEventListener("resize", ()=>{
        if (window.matchMedia("(orientation: landscape)").matches) {
            setCardWidth("55vh")
        }else{
            setCardWidth("85vw")
        }
      });

    return (
        <div className={classes.cardContainer} onClick={()=>props.history.push("/projects/" + props.link)}>
            <img style={{width:cardWidth, height:cardWidth}} className={classes.img} src={props.image} alt={props.text}/>
            <p style={{width:cardWidth}} className={classes.text}>{props.text}</p>
        </div>
  );
}

const useStyles = makeStyles((theme) => ({
    '@global': {
        '*::-webkit-scrollbar': {
          width: '0.4em'
        },
        '*::-webkit-scrollbar-track': {
        '-webkit-box-shadow': 'inset 0 0 6px rgba(0,0,0,0.00)'
        },
        '*::-webkit-scrollbar-thumb': {
        backgroundColor: 'rgba(0,0,0,.1)',
        outline: '1px solid slategrey'
        }
    },
    root: {
    },
    title:{
        color: Constant.TEXT_COLOR_1,
        left:0,
        right:0,
        textAlign:"center",
        position: "absolute",
        margin:0,
        fontSize:'300%',
        fontWeight:"bolder",
        zIndex: 50,
    },
    gridList: {
        // flexWrap: 'nowrap',
        position:"absolute",
        zIndex:50,
        width:"85vw",
        left:"10vw",
    }

}))

function Projects(props) {
    const classes = useStyles(props);  

    const verticalGridListStyle = {
        top:"20vh",
        overflowX:"hidden",
        height:"80vh",
    }

    const horizontalGridListStyle = {
        top:"20vw",
        flexWrap: 'nowrap',
        overflowY:"hidden",
        height:"60vh",
    }
    
    const [titleTop, setTitleTop] = React.useState(window.matchMedia("(orientation: landscape)").matches? "10vw" : "10vh");
    const [gridListStyle, setGridListStyle] = React.useState(window.matchMedia("(orientation: landscape)").matches? horizontalGridListStyle : verticalGridListStyle);

    window.addEventListener("resize", ()=>{
        if (window.matchMedia("(orientation: landscape)").matches) {
            setGridListStyle(horizontalGridListStyle)
            setTitleTop("10vw")
        }else{
            setGridListStyle(verticalGridListStyle)
            setTitleTop("10vh")
        }
      });
    return (
        <div className={classes.root}>
            <AnimatedCircle bottom="-30%" style={{height:"100%"}}>
            <h1 className={classes.title} style={{top:titleTop}}>
                MY WORK
            </h1>

            <GridList className={classes.gridList} style={{...gridListStyle}} cols={2.5}>
                {
                    Constant.PROJECT_DATA.map((d, index)=>(
                        <CustomCard {...d} {...props} key={index}></CustomCard>
                    ))
                }
            </GridList>

            </AnimatedCircle>
        </div>
    );
  }
  
export default Projects;