import React from 'react';
import axios from 'axios';
import { makeStyles } from '@material-ui/core/styles';
import { Button,  Dialog, DialogContent, IconButton, Typography, AppBar, Toolbar, Backdrop } from '@material-ui/core';
import CircularProgress from '@material-ui/core/CircularProgress';
import CloseIcon from '@material-ui/icons/Close';
import CustomSnackbar from '../../Components/CustomSnackbar';
import RotateLeftIcon from '@material-ui/icons/RotateLeft';


const useStyles = makeStyles((theme) => ({
    root:{
        height: "90vh",
        width: "90vw",
        // marginLeft:"10vw",
        // marginTop:"10vh",
        background:"linear-gradient(170deg, #000000 0%, #04619f 74%)"
    },
    paragraph:{
        color:"#FFF",
    },
    uploadButton:{
        display:"block",
        position:"absolute",
        bottom:"5vh",
        marginLeft:"auto",
        marginRight:"auto",
        left:0,
        right:0,
    }
}))

function handleUpload(files, setResImg, setIsLoading, setSnackbarSeverity, setSnackbarMsg, setSnackbarOpen){
    if (files.length === 0){
        return
    }
    setIsLoading(true)
    let form_data = new FormData();
    let file = files[0]
    form_data.append('image', file);

    axios.post("https://cryptic-thicket-91896.herokuapp.com/detect", form_data, {
        headers: {
          'content-type': 'multipart/form-data'
        }
    })
    .then(res => {
        setResImg("data:image/png;base64, " + res.data.result)
        setIsLoading(false)
        setSnackbarOpen(true)
        setSnackbarMsg("Upload Success!")
        setSnackbarSeverity("success")
    })
    .catch(err => {
        console.log(err)
        setIsLoading(false)
        setSnackbarOpen(true)
        setSnackbarMsg("Upload Error!")
        setSnackbarSeverity("error")
    })
}



function IntroPage(props){
    const {paragraph} = useStyles(props);      
    return(
        <>
            <Typography component={'p'} variant={'body1'} className={paragraph}>
                This Face Detector API uses You Only Look Once tiny (YOLOv4-tiny), a small version of 
                YOLOv4 designed for mobile devices. Such networks designed are less accurate but 
                speed-optimised to be able to run in real-time. Click on "Upload Image" or "Take a Picture" 
                to test out my Face Detector API.
            </Typography>
            <br/>
            <Typography component={'p'} variant={'body1'} className={paragraph}>
                
            </Typography>
            <Typography component={'p'} variant={'body1'} className={paragraph}>
                <b>This website does not store any uploaded images.</b> Storage
                is not possible, because there isn't a database to store it. 
                That's why what happens on your computer stays on your computer. Period.  
            </Typography>
        </>
    )
}

function FaceLmkCustomDialog(props){
    const [resImg, setResImg] = React.useState(null)
    const [isLoading, setIsLoading] = React.useState(false)
    const [snackbaropen, setSnackbarOpen] = React.useState(false)
    const [snackbarMsg, setSnackbarMsg] = React.useState("")
    const [snackbarSeverity, setSnackbarSeverity] = React.useState("")
    const [wakeup, setWakeup] = React.useState(false)
    const classes = useStyles(props);    

    const hiddenFileInput = React.useRef(null);

    const handleClick = event => {
        hiddenFileInput.current.click();
    };

    function handleChange (event, setSnackbarSeverity, setSnackbarMsg, setSnackbarOpen) {
        const fileUploaded = event.target.files;
            console.log(fileUploaded[0].size)

        if(fileUploaded[0].size > 1048576){
            setSnackbarSeverity("error")
            setSnackbarMsg("File must not exceed 1MB!")
            setSnackbarOpen(true)
            return
        }
        if(fileUploaded[0].type !== "image/jpeg"){
            setSnackbarSeverity("error")
            setSnackbarMsg("Uploaded file is not a valid image!")
            setSnackbarOpen(true)
            return
        } 
        
        handleUpload(fileUploaded, setResImg, setIsLoading, setSnackbarSeverity, setSnackbarMsg, setSnackbarOpen)
    };
    
    if(!wakeup){
        axios.post("https://cryptic-thicket-91896.herokuapp.com/open_sesame", {}, {})
        setWakeup(true)
    }

    function handleClose(){
        setWakeup(false)
        props.setDialogOpen(false)
    }

    return (
        <Dialog PaperProps={{className:classes.root}} fullScreen onClose={handleClose} aria-labelledby="simple-dialog-title" open={props.open}>
                <CustomSnackbar setSnackbarOpen={setSnackbarOpen} text={snackbarMsg} open={snackbaropen} severity={snackbarSeverity}/>
                <AppBar elevation={0} style={{position: 'relative', zIndex: 30, color: '#FFF', background:"rgba(0,0,0,0)"}}>
                    <Toolbar>
                        <IconButton edge="start" color="inherit" onClick={handleClose} aria-label="close">
                            <CloseIcon />
                        </IconButton>
                        <Typography variant="h6">
                            Face Detection API
                        </Typography>
                    </Toolbar>
                </AppBar>
                <DialogContent>
                    <Backdrop style={{zIndex: 20, color: '#fff'}} open={isLoading}>
                        <Typography component={'span'} variant={'body1'}>
                            Uploading image... Please wait...  
                        </Typography>
                        <CircularProgress color="inherit" style={{marginLeft:"10px"}} />
                    </Backdrop>

                    {
                        resImg === null?
                        <>
                            <IntroPage/>
                            <Button onClick={handleClick} className={classes.uploadButton} variant="contained" color="primary" >Upload Image</Button>
                            <input 
                                type="file" 
                                accept="image/*"
                                maxSize={1048576}
                                ref={hiddenFileInput}
                                onChange={(e) => handleChange(e, setSnackbarSeverity, setSnackbarMsg, setSnackbarOpen)}
                                style={{display:'none'}} 
                            /> 
                        </>
                        :
                        <>
                            <img src={resImg} style={{maxWidth: "100vw",position:"absolute", left:0}} alt={"Uploaded picture with face landmarks"}/>
                            <Button onClick={()=>setResImg(null)} style={{left:0, position:"absolute", bottom:0, width:"100%"}}  color="primary" variant="contained">
                                <RotateLeftIcon style={{marginRight:"5px"}}/>
                                Reset 
                            </Button>
                        </>
                    }
                </DialogContent>
        </Dialog>
    )
}

export default FaceLmkCustomDialog