import React from 'react';
import { makeStyles } from '@material-ui/core/styles';
import * as Constant from "../Constants"
import {TextField, Button } from '@material-ui/core';
import AnimatedCircle from '../Components/AnimatedCircle';
import CustomSnackbar from '../Components/CustomSnackbar';
import { useForm } from "react-hook-form";
import emailjs, { init } from 'emailjs-com';
init("user_bV7M7p1wEvoMEjiwAc4EF");

const useStyles = makeStyles((theme) => ({
    root: {
        
    },
    title:{
        color: Constant.TEXT_COLOR_1,
        textAlign:"center",
        fontSize:'300%',
        fontWeight:"bolder",
        zIndex: 50,
    },
    form:{
        display:"block",
        float:"left",
        width:"80vw",
        marginLeft:"10vw",
        background:"#FFFFFF",
        borderRadius:"50px",
        zIndex:100,
    },
    textfield:{
        zIndex:30,
        marginLeft:"10vw",
        width:"60vw",
        margin: "0%",
    },
    socialContainer:{
        display:"block",
        position: "absolute",
        color:"#FFF",
        bottom:0,
    },
}));

function CustomTextField(props){
    let error_msg = "Please provide a "
    let inputRef
    
    if (props.id.toLowerCase() === "email"){
        error_msg = "Please provide a valid "
        inputRef = props.register({
            required: "wasd",
            pattern: /^(([^<>()[\]\\.,;:\s@\"]+(\.[^<>()[\]\\.,;:\s@\"]+)*)|(\".+\"))@((\[[0-9]{1,3}\.[0-9]{1,3}\.[0-9]{1,3}\.[0-9]{1,3}\])|(([a-zA-Z\-0-9]+\.)+[a-zA-Z]{2,}))$/
        })
    } else{
        inputRef = props.register({
            required: "wasd",
        })
    }

    error_msg = error_msg + props.id.toLowerCase()


    return (
        <TextField
            size="small"
            className={props.className}
            style={props.style}
            id={props.id.toLowerCase()}
            name={props.id.toLowerCase()}
            label={props.id}
            inputRef={inputRef}
            variant="filled"
            multiline={props.multiline}
            rows={props.rows}
            helperText={props.isError ? error_msg : " "}
            error={props.isError}
        />
    )
}

function Contact(props) {
  const classes = useStyles();
  const { register, handleSubmit, reset, errors } = useForm();
  const [snackbaropen, setSnackbarOpen] = React.useState(false);
  const [snackbarMsg, setSnackbarMsg] = React.useState("");
  const [snackbarSeverity, setSnackbarSeverity] = React.useState("");

  const onSubmit = (templateParams) => {
    emailjs.send("service_n470o7l", "template_5ol99j2", templateParams)
        .then((result) => {
            console.log(result.text);
            setSnackbarOpen(true);
            setSnackbarMsg("Thank you! Your message has been sent!")
            setSnackbarSeverity("success")
            reset()
        }, (error) => {
            console.log(error.text);
            setSnackbarMsg("Failed to send your message. Please try again later")
            setSnackbarSeverity("error")
            setSnackbarOpen(true);
        });
    };

  const socialList = Constant.SOCIAL_LIST
  const socialProps = {
      position:"absolute",
      bottom:0
  }

  const [screenHeight, setScreenHeight] = React.useState(window.matchMedia("(orientation: landscape)").matches? "100vw" : "100vh");
  const [titleTop, setTitleTop] = React.useState(window.matchMedia("(orientation: landscape)").matches? "10vw" : "10vh");

  window.addEventListener("resize", ()=>{
    if (window.matchMedia("(orientation: landscape)").matches) {
        setScreenHeight("100vw")
        setTitleTop("10vw")
    }else{
        setScreenHeight("100vh")
        setTitleTop("10vh")
    }
  });

  return (
        <AnimatedCircle bottom="-30%" style={{height:screenHeight}}>
            <h1 className={classes.title} style={{marginTop:titleTop}}>Contact Me</h1>
            <form className={classes.form} onSubmit={handleSubmit(onSubmit)}>
                <CustomTextField id="Name" style={{marginTop:"5vw"}} isError={!!errors.name} className={classes.textfield} register={register}/>
                <CustomTextField id="Email" isError={!!errors.email} className={classes.textfield} register={register}/>
                <CustomTextField id="Subject" isError={!!errors.subject} className={classes.textfield} register={register}/>
                <CustomTextField id="Message" isError={!!errors.message} multiline={true} rows={4} className={classes.textfield} register={register}/>
                <Button className={classes.textfield} style={{marginBottom:"5vw"}} type="submit" onClick={()=> handleSubmit(onSubmit)} variant="contained" color="primary">Send Message</Button>
            </form>
            <CustomSnackbar setSnackbarOpen={setSnackbarOpen} text={snackbarMsg} open={snackbaropen} severity={snackbarSeverity} />

            <div className={classes.socialContainer}>
                {Object.keys(socialList).map((text, index) => (
                    <div style={{...socialProps, marginLeft:(index*10).toString() + "vw"}} key={text} onClick={() => window.open(socialList[text]['link'],'_blank')}>
                        {socialList[text]['largeIcon']}
                    </div>
                  ))}
            </div>
        </AnimatedCircle>
  );
}

export default Contact